<template>
  <div class="levertransactions">
    <ao-nav :ao-style="{'background': '#1b2945'}"></ao-nav>
    <div class="box">
      <div class="h">
        <div class="t">{{ lt_txt[0] }}{{ hazard_rate }}</div>
        <div class="t">
          {{ lt_txt[1] }}
          <span class="green">{{ all_profit_loss }}</span>
        </div>
        <el-button @click="position(-1)">{{ lt_btn[0] }}</el-button>
      </div>
      <div class="table">
        <div class="th">
          <td
            class="td"
            :class="[index === 0 || index === lt_td.length - 1 ? 'td-2' : '' ]"
            v-for="(item, index) in lt_td"
            :key="index"
          >{{ item }}</td>
          <!-- 
          <td class="td td-2">类型</td>
          <td class="td">开仓价</td>
          <td class="td">当前价</td>
          <td class="td">保证金</td>
          <td class="td">止盈价</td>
          <td class="td">止损价</td>
          <td class="td">开仓时间</td>
          <td class="td">手续费</td>
          <td class="td">隔夜费</td>
          <td class="td">盈亏</td>
          <td class="td td-2">操作</td>-->
        </div>
        <div class="tr" v-for="(item, index) in list" :key="index">
          <td class="td td-2">{{ item.type_text }}</td>
          <td class="td">{{ ffff(item.position_price) }}</td>
          <td class="td">{{ ffff(item.new_price) }}</td>
          <td class="td">{{ ffff(item.margin) }}</td>
          <td class="td">{{ item.stop_profit }}</td>
          <td class="td">{{ item.stop_loss }}</td>
          <td class="td">{{ item.open_time }}</td>
          <td class="td">{{ ffff(item.open_fee) }}</td>
          <td class="td">{{ ffff(item.overnight_fee) }}</td>
          <td class="td">{{ ffff(item.profit_loss) }}</td>
          <td class="td td-2">
            <span class="btn" @click="setpice(index)">{{ lt_btn[1] }}</span>
            <span class="btn" @click="position(index)">{{ lt_btn[2] }}</span>
          </td>
          <td class="td">{{ item.hazard_rate }}</td>
        </div>
      </div>
      <div class="no-data">{{ list.length === 0 ? node_data : '' }}</div>
    </div>
    <el-dialog
      class="ao-el-dialog"
      width="40%"
      top="10vh"
      :title="lt_dl[0]"
      :center="false"
      :visible.sync="dialogVisible"
    >
      <el-form ref="form" :model="form" label-width="100px">
        <el-form-item :label="lt_dl[1]">
          <el-input v-model="form.stop_profit"></el-input>
        </el-form-item>
        <el-form-item :label="lt_dl[2]">
          <el-input v-model="form.stop_loss"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="save">{{ lt_btn[3] }}</el-button>
        <el-button @click="dialogVisible = false">{{ lt_btn[4] }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import AoNav from "@/components/AoNav";
import { getPositionList, closePosition, editPosition } from "@/api/asset";
var __timer__ = null;
export default {
  components: {
    AoNav
  },
  data() {
    return {
      lt_btn: this.$t("deal.lt_btn"), //["一键平仓", "设置止盈止损价", "平仓", "立即提交", "返回"],
      lt_txt: this.$t("deal.lt_txt"), //["风险率：", "持仓总盈亏："],
      lt_td: this.$t("deal.lt_td"), //[
      //   "类型",
      //   "开仓价",
      //   "当前价",
      //   "保证金",
      //   "止盈价",
      //   "止损价",
      //   "开仓时间",
      //   "手续费",
      //   "隔夜费",
      //   "盈亏",
      //   "操作"
      // ],
      lt_dl: this.$t("deal.lt_dl"), //["设置止盈止损价", "止盈价", "止损价"],
      form: {},
      dialogVisible: false,
      list: [],
      all_profit_loss: "0",
      hazard_rate: "0",
      node_data: this.$t("deal.node_data") // "暂无数据",
    };
  },
  mounted() {
    __timer__ && clearInterval(__timer__);
    this.currency = localStorage.getItem("web-currency");
    if (!this.currency) this.$router.back(-1);
    this.getPositionList();
    __timer__ = setInterval(() => {
      this.getPositionList();
    }, 5000);
  },
  destroyed() {
    __timer__ && clearInterval(__timer__);
  },
  methods: {
    ffff(v, l = 4) {
      return parseFloat(v).toFixed(l);
    },
    save() {
      editPosition({
        id: this.form.id,
        stop_profit: this.form.stop_profit,
        stop_loss: this.form.stop_loss
      }).then(res => {
        this.dialogVisible = false;
      });
    },
    setpice(index) {
      this.dialogVisible = true;
      this.form = this.list[index];
    },
    getPositionList() {
      var data = {
        currency: this.currency,
        is_pl_r: 1
      };
      getPositionList(data).then(res => {
        this.all_profit_loss = res.all_profit_loss;
        this.hazard_rate = res.hazard_rate;
        this.list = res.list;
      });
    },
    position(index) {
      var data = { id: index === -1 ? -1 : this.list[index].id };
      if (index === -1) {
        data.currency = this.currency;
      }
      closePosition(data).then(res => {
        this.list.splice(index, 1);
      });
    }
  }
};
</script>

<style  lang="scss">
.levertransactions {
  .no-data {
    color: #999;
    font-size: 14px;
    text-align: center;
    margin-top: 16px;
  }
  div {
    box-sizing: border-box;
  }

  .box {
    min-height: 500px;
    background: #1a243b;
    width: 97%;
    margin: 30px auto;
    padding: 30px;
    .h {
      position: relative;
      .t {
        color: #c7cce6;
        margin-bottom: 15px;
        span.green {
          color: #0d8551;
        }
      }
    }
    .el-button {
      position: absolute;
      right: 0;
      border-radius: 3px;
      color: #fff;
      background-color: #638bd4;
      cursor: pointer;
      min-height: 33px;
      min-width: 80px;
      font-size: 14px;
      border: none;
      padding: 0 5px;
      line-height: 33px;
      text-align: center;
      top: 33px;
    }
  }
  .th,
  .tr {
    display: flex;
    padding: 8px 0;
    color: #637085;
  }
  .th {
    border-bottom: 1px solid #2e333c;
  }
  .td {
    flex: 1;
  }
  .td-2 {
    flex: 2;
  }
  .th .td,
  .tr .td {
    color: #637085;
    font-size: 14px;
    text-align: center;
  }
  .btn,
  .text {
    border-radius: 3px;
    color: #fff;
    cursor: pointer;
    font-size: 14px;
    border: none;
    padding: 0 5px;
    line-height: 33px;
    text-align: center;
  }
  .btn {
    background-color: #638bd4;
  }
}
</style>



